'use client'

import React, { useEffect, useRef, useState } from 'react'
import { Button } from './ui/button'
import * as faceapi from 'face-api.js'

interface AttendanceRecord {
  id: number
  timestamp: string
  confidence: number
}

export default function FaceScanAttendance() {
  const videoRef = useRef<HTMLVideoElement>(null)
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [attendanceMarked, setAttendanceMarked] = useState(false)
  const [scanning, setScanning] = useState(false)
  const [modelsLoaded, setModelsLoaded] = useState(false)
  const [detectionInterval, setDetectionInterval] = useState<NodeJS.Timeout | null>(null)

  useEffect(() => {
    loadModels()
    return () => {
      // Cleanup on unmount
      if (detectionInterval) {
        clearInterval(detectionInterval)
      }
      stopVideo()
    }
  }, [])

  const loadModels = async () => {
    try {
      setLoading(true)
      const MODEL_URL = '/models'
      
      // Load required models
      await Promise.all([
        faceapi.nets.tinyFaceDetector.loadFromUri(MODEL_URL),
        faceapi.nets.faceLandmark68Net.loadFromUri(MODEL_URL),
        faceapi.nets.faceRecognitionNet.loadFromUri(MODEL_URL),
      ])
      
      setModelsLoaded(true)
      setLoading(false)
    } catch (err) {
      console.error('Error loading models:', err)
      setError('Failed to load face detection models. Please ensure model files are available.')
      setLoading(false)
    }
  }

  const startVideo = async () => {
    if (!modelsLoaded) {
      setError('Face detection models are not loaded yet.')
      return
    }

    try {
      const stream = await navigator.mediaDevices.getUserMedia({ 
        video: { 
          width: 640, 
          height: 480,
          facingMode: 'user'
        } 
      })
      
      if (videoRef.current) {
        videoRef.current.srcObject = stream
        videoRef.current.play()
        setScanning(true)
        setError(null)
        
        // Start face detection after video is playing
        videoRef.current.addEventListener('playing', () => {
          startFaceDetection()
        })
      }
    } catch (err) {
      console.error('Error accessing webcam:', err)
      setError('Camera access denied or not available. Please allow camera permissions.')
    }
  }

  const startFaceDetection = () => {
    if (!videoRef.current || !canvasRef.current) return

    const video = videoRef.current
    const canvas = canvasRef.current
    
    // Set canvas dimensions to match video
    canvas.width = video.videoWidth
    canvas.height = video.videoHeight

    const interval = setInterval(async () => {
      try {
        const detections = await faceapi
          .detectAllFaces(video, new faceapi.TinyFaceDetectorOptions())
          .withFaceLandmarks()
          .withFaceDescriptors()

        // Clear previous drawings
        const ctx = canvas.getContext('2d')
        if (ctx) {
          ctx.clearRect(0, 0, canvas.width, canvas.height)
        }

        if (detections && detections.length > 0) {
          // Draw face detection boxes
          faceapi.draw.drawDetections(canvas, detections)
          faceapi.draw.drawFaceLandmarks(canvas, detections)

          // Mark attendance for the first detected face
          const detection = detections[0]
          const confidence = detection.detection.score

          if (confidence > 0.7) { // Confidence threshold
            markAttendance(confidence)
            clearInterval(interval)
            setDetectionInterval(null)
          }
        }
      } catch (err) {
        console.error('Detection error:', err)
        setError('Face detection failed. Please try again.')
        clearInterval(interval)
        setDetectionInterval(null)
      }
    }, 100) // Check every 100ms for smooth detection

    setDetectionInterval(interval)
  }

  const markAttendance = (confidence: number) => {
    const record: AttendanceRecord = {
      id: Date.now(),
      timestamp: new Date().toISOString(),
      confidence: Math.round(confidence * 100)
    }

    // Save to localStorage (in production, this would be an API call)
    try {
      const existingRecords = localStorage.getItem('attendanceRecords')
      const records = existingRecords ? JSON.parse(existingRecords) : []
      records.push(record)
      localStorage.setItem('attendanceRecords', JSON.stringify(records))
    } catch (err) {
      console.error('Error saving attendance record:', err)
    }

    setAttendanceMarked(true)
    stopVideo()
    setScanning(false)
  }

  const stopVideo = () => {
    if (videoRef.current && videoRef.current.srcObject) {
      const stream = videoRef.current.srcObject as MediaStream
      stream.getTracks().forEach(track => track.stop())
      videoRef.current.srcObject = null
    }
  }

  const resetScan = () => {
    setAttendanceMarked(false)
    setError(null)
    setScanning(false)
    if (detectionInterval) {
      clearInterval(detectionInterval)
      setDetectionInterval(null)
    }
    stopVideo()
  }

  return (
    <div className="flex flex-col items-center space-y-6">
      {loading && (
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-black dark:border-white mx-auto mb-4"></div>
          <p>Loading face detection models...</p>
        </div>
      )}

      {error && (
        <div className="text-center p-4 border border-red-200 dark:border-red-800 rounded-lg bg-red-50 dark:bg-red-950">
          <p className="text-red-600 dark:text-red-400">{error}</p>
          <Button 
            onClick={resetScan} 
            variant="outline" 
            className="mt-4"
          >
            Try Again
          </Button>
        </div>
      )}

      {attendanceMarked && (
        <div className="text-center p-6 border border-green-200 dark:border-green-800 rounded-lg bg-green-50 dark:bg-green-950">
          <h3 className="text-lg font-semibold text-green-800 dark:text-green-200 mb-2">
            Attendance Marked Successfully!
          </h3>
          <p className="text-green-600 dark:text-green-400 mb-4">
            Your attendance has been recorded at {new Date().toLocaleString()}
          </p>
          <div className="flex gap-4 justify-center">
            <Button onClick={resetScan}>
              Scan Again
            </Button>
            <Button variant="outline" onClick={() => window.location.href = '/history'}>
              View History
            </Button>
          </div>
        </div>
      )}

      {!loading && !error && !attendanceMarked && (
        <div className="text-center space-y-4">
          {!scanning ? (
            <Button onClick={startVideo} size="lg">
              Start Camera
            </Button>
          ) : (
            <div className="space-y-4">
              <p className="font-medium text-lg">
                Scanning for faces... Please look at the camera
              </p>
              <Button onClick={resetScan} variant="outline">
                Stop Scanning
              </Button>
            </div>
          )}
        </div>
      )}

      {/* Video and Canvas Container */}
      <div className="relative">
        <video 
          ref={videoRef} 
          className={`rounded-lg border-2 border-gray-200 dark:border-gray-800 ${
            scanning ? 'block' : 'hidden'
          }`}
          width="640" 
          height="480" 
          autoPlay 
          muted 
          playsInline
        />
        <canvas 
          ref={canvasRef}
          className={`absolute top-0 left-0 ${scanning ? 'block' : 'hidden'}`}
        />
      </div>

      {/* Instructions */}
      {scanning && (
        <div className="text-center max-w-md">
          <h4 className="font-medium mb-2">Instructions:</h4>
          <ul className="text-sm text-gray-600 dark:text-gray-400 space-y-1">
            <li>• Position your face clearly in the camera view</li>
            <li>• Ensure good lighting for better detection</li>
            <li>• Stay still for a few seconds</li>
            <li>• Attendance will be marked automatically</li>
          </ul>
        </div>
      )}
    </div>
  )
}
